function sliderMain() {
    var swiper = new Swiper('.main .swiper', {
        slidesPerView: '1',
        speed: 1000,
        autoplay: {
            delay: 3000,
            disableOnInteraction: false
        },
        navigation: {
            nextEl: '.main .swiper-button-next',
            prevEl: '.main .swiper-button-prev',
        },
        pagination: {
            el: ".main .swiper-pagination",
            clickable: true,
        },
    })
}

let SlidersArrayAdvantages = []
function sliderAdvantages() {
    $(".advantages").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArrayAdvantages[index] = new Swiper(`.advantages[data-slider-id="${index}"] .swiper`, {
            slidesPerView: '4',
            spaceBetween: 30,
            navigation: {
                nextEl: `.advantages[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.advantages[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `.advantages[data-slider-id="${index}"] .swiper-pagination`,
                clickable: true,
            },
            breakpoints: {
                320: {
                    slidesPerView: '1',
                },
                576: {
                    slidesPerView: '2',
                },
                769: {
                    slidesPerView: '3',
                },
                993: {
                    slidesPerView: '4',
                }
            }
        })
    })
}

