let SlidersArray = []
function sliderImages() {
    $(".images-slider").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.images-slider[data-slider-id="${index}"] .swiper`, {
            slidesPerView: 1,
            spaceBetween: 15,
            pagination: {
                el: `.images-slider[data-slider-id="${index}"] .images__pagination`,
                clickable: true,
            },
        })
    })
}

