$(document).ready(function() {

    sliderMain();
    sliderAdvantages();
    sliderCatalog2();
    sliderCatalog3();
    sliderImages();
    sliderDecoration();
    sliderInfo();


    // Header - Fixed
    var headerFixed = $('.header-fixed');
    $(window).scroll(function() {
        if ($(window).scrollTop() > 160) {
            headerFixed.addClass('header-fixed--active');
        } else {
            headerFixed.removeClass('header-fixed--active');
        }
    });

    // Menu
    $('.header-top__burger').click(function () {
        $('.menu').addClass('menu--active')
        $('.bg-menu').addClass('bg-menu--active')
        $('body').addClass('fixed-body')
    })

    $('.menu__close').click(function () {
        $('.menu').removeClass('menu--active')
        $('.bg-menu').removeClass('bg-menu--active')
        $('body').removeClass('fixed-body')
    })

    // Search
    $('.search-button').click(function () {
        $(this).parent('.header-top__search').addClass('header-top__search--active')
    })

    let searchButton = $('.search-button')
    let search = $('.header-top__search')
    let searchResults = $('.search-results')

    $(document).mouseup(function (e) {
        if ( !searchButton.is(e.target) && searchButton.has(e.target).length === 0 &&
            !search.is(e.target) && search.has(e.target).length === 0 &&
            !searchResults.is(e.target) && searchResults.has(e.target).length === 0

        ) {
            search.removeClass('header-top__search--active')
            searchResults.removeClass('search-results--active')
        }
    })

    $(".search-input").on("input", function() {
        let val = $(this).val()

        if (val.length > 2) {
            $(".search-results").addClass('search-results--active')
        } else {
            $(".search-results").removeClass('search-results--active')
        }
    })

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs").find(".tabs-content").removeClass("tabs-content--active")
        $(this).parents(".tabs").find(`.tabs-content[data-content-path="${path}"]`).addClass("tabs-content--active")
    })

    $(".advantages-slide").hover(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".advantages-slide").removeClass("advantages-slide--active")
        $(this).addClass("advantages-slide--active")

        $(this).parents(".advantages__inner").find(".advantages__card").removeClass("advantages__card--active")
        $(this).parents(".advantages__inner").find(`.advantages__card[data-content-path="${path}"]`).addClass("advantages__card--active")
    })

    // Select
    $(".custom_select select").niceSelect()

    $('.select__list-default').hover(function () {
        $(this).parent('.select__wrap').addClass('select__wrap--active')
    })

    $('.select__list li').click(function () {
        var currentele = $(this).html()
        $(this).parents('.select__wrap').find('.select__current').html(currentele)
        $(this).parents('.select__wrap').removeClass('select__wrap--active')
        $(this).siblings(".select__list li").removeClass("li--active")
        $(this).addClass("li--active")
    })

    let selectLi = $('.select__list li')
    let selectWrap = $('.select__wrap')

    $(document).mouseover(function (e) {
        if ( !selectLi.is(e.target) && selectLi.has(e.target).length === 0 &&
            !selectWrap.is(e.target) && selectWrap.has(e.target).length === 0

        ) {
            selectWrap.removeClass('select__wrap--active')
        }
    })

    //
    $('.arrow-down').click(function () {
        $(this).siblings('.hide').toggleClass('hide--active')
    })

    let arrowDown = $('.arrow-down')
    let hide = $('.hide')

    $(document).mouseup(function (e) {
        if ( !arrowDown.is(e.target) && arrowDown.has(e.target).length === 0 &&
            !hide.is(e.target) && hide.has(e.target).length === 0

        ) {
            hide.removeClass('hide--active')
        }
    })

    // Favorite
    $('.favorite').click(function () {
        $(this).toggleClass('favorite--active')
    })

    // Accordion
    if (window.matchMedia("(max-width: 576px)").matches) {
        $(".footer-top__item-show").click((function(){
        $(this).toggleClass("footer-top__item-show--active"),
        $(this).siblings(".footer-top__item-hidden").slideToggle()}));
    }

    // Catalog
    $('.header-bottom__catalog').hover(function () {
        $(this).addClass('header-bottom__catalog--active')
        $('.main-bg').addClass('main-bg--active')
    })

    let CatalogItem = $('.header-bottom__catalog')
    let mainBg = $('.main-bg')

    $(document).mouseover(function (e) {
        if ( !CatalogItem.is(e.target) && CatalogItem.has(e.target).length === 0
        ) {
            CatalogItem.removeClass('header-bottom__catalog--active')
            mainBg.removeClass('main-bg--active')
        }
    })


    $('.catalog2__item').hover(
        function () {
            $(this).children('.catalog2__show').addClass('catalog2__show--active')
            $(this).children('.catalog2__hidden').addClass('catalog2__hidden--active');
        },
        function () {
            $(this).children('.catalog2__show').removeClass('catalog2__show--active')
            $(this).children('.catalog2__hidden').removeClass('catalog2__hidden--active');
        }
    );

    //
    $('.mob-catalog__btn').click(function () {
        $('.mob-catalog').addClass('mob-catalog--active')
        $('.bg-menu').addClass('bg-menu--active')
        $('body').addClass('fixed-body')
    })

    $('.mob-catalog__close').click(function () {
        $('.mob-catalog').removeClass('mob-catalog--active')
        $('.bg-menu').removeClass('bg-menu--active')
        $('body').removeClass('fixed-body')
    })

    // Up
    var btn = $('.up');
    $(window).scroll(function() {
        if ($(window).scrollTop() > 300) {
            btn.addClass('up--active');
        } else {
            btn.removeClass('up--active');
        }
    });
    btn.on('click', function(e) {
        e.preventDefault();
        $('html, body').animate({scrollTop:0}, '300');
    });

    // advantages-slide
    $('.advantages-slide').hover(
        function () {
            $('.advantages__slider-btns').addClass('btns--active');
        },
        function () {
            $('.advantages__slider-btns').removeClass('btns--active');
        }
    );

    // modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }


})
